.registration-form {
  &__container {
    display: grid;
    grid-template-rows: auto 1fr auto;
    height: 80vh;
  }

  &__form {
    padding-top: 4.5vh;
    grid-column: 2;
    width: 90vw;
    flex-direction: column;
    display: flex;
    align-items: center;
  }

  &__bottom {
    flex-direction: column;
    display: flex;
    grid-column: 2;
    width: 90vw;
    //align-items: center;

    &-redirect {
      margin-top: 7px;
      font-size: 14px;
      font-weight: 300;
      color: #8C89B4;
    }
  }
}


@media only screen and (min-width: 480px) {
  .registration-form {
    &__container {
      height: 70vh;
    }
    &__form {
      width: 345px;
      align-items: center;
    }
    &__bottom {
      width: 345px;

    }
  }
}

