.confirm-registration {
  &__container {
    display: grid;
    grid-template-rows: auto 1fr auto;
    height: 80vh;
  }

  &__form {
    grid-column: 2;
    width: 90vw;
    flex-direction: column;
    display: flex;
    align-items: center;
    text-align: center;

    &-otp {
      margin-top: 22vh;

      & span {
        font-weight: 300;
        font-size: 16px;
        color: #3eb092;
      }

      & div {
        margin-top: 1vh;
      }
    }

    & h2 {
      padding: 4.5vh 0;
      font-size: 20px;
      font-weight: 300;
    }

    & h3 {
      font-size: 16px;
      font-weight: 300;
      width: 270px;
    }
  }

  &__bottom {
    flex-direction: column;
    display: flex;
    align-items: center;
    grid-column: 2;
    width: 90vw;
    padding-bottom: 23px;
  }
}

@media only screen and (min-width: 480px) {
  .confirm-registration {
    &__container {
      height: 70vh;
    }
    &__bottom,
    &__form {
      width: 345px;
      align-items: center;


      &-otp {
        margin-top: 10vh;
        width: 345px;

      }
    }
  }
}
