.home-page {
  &__wrapper {
    max-width: 100vw;
    height: 100vh;
    border: 1px solid black;
    background-image: url('../../assets/images/background/home-page.png');
    background-repeat: no-repeat;
    background-size: cover;
    margin: auto;
  }

  &__container {
    display: grid;
    grid-template-rows: auto 1fr auto;
    height: 70vh;
    padding-top: 18.2vh;

    &-title {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;

      h2,
      h3 {
        font-weight: 300;
        font-size: 36px;
        line-height: 24px;
        color: #fff;
      }

      h3 {
        font-size: 24px;
      }
    }

    &-buttons {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 12px;
      padding-bottom: 23px;

      & a,
      u {
        text-decoration: none;
      }
    }
  }
}

@media only screen and (min-width: 480px) {
  .home-page {
    &__container {
      height: 60vh;
    }
  }
}
