.back-home {
  height: 24px;
  display: flex;
  align-items: center;
  gap: 19px;
  z-index: 100;
  grid-column: 2;
  width: 90vw;

  &__title {
    font-size: 20px;
    line-height: 4.8vw;
    font-weight: 400;
  }
}

@media only screen and (min-width: 480px) {
  .back-home {
    margin-top: 10vh;
    width: 345px;
  }
}
